FCBH.docReady(() => {
    new SummitPartTwo();
});

class SummitPartTwo {
    constructor() {
        this.paymentForm = this.getPaymentForm()
        const isValid = this.validatePaymentForm(this.paymentForm)
        if(isValid){
            this.createHiddenInputs()
        }
    }

    validatePaymentForm({ currency_code, amounts, recurring_donation, program, regions }) {   
        if (!currency_code || !amounts || !recurring_donation || !program || !regions) {
            window.location = "/chosen-summit-2020";
            return false;
        }
        return true;
    }

    getPaymentForm() {
        return FCBH.getFromStorage(FCBH.GIVE_FORM) || {};
    }

    createHiddenInputs(){
        const form = document.querySelector('#wf-form-Chosen-Summit-Personal-Info-Form')

        const regionInput = this.createRegionInput()
        const programInput = this.createProgramInput()
        const whenInput = this.createWhenInput()
        const amountInput = this.createAmountInput()

        form.prepend(regionInput, programInput, whenInput, amountInput)
    }

    createRegionInput(){
        const regionInput = document.createElement('input')
        regionInput.type = 'hidden'
        regionInput.name = 'region'
        regionInput.id = 'region'
        regionInput.value = this.paymentForm.regions
        return regionInput
    }

    createProgramInput(){
        const programInput = document.createElement('input')
        programInput.type = 'hidden'
        programInput.name = 'program'
        programInput.id = 'program'
        programInput.value = this.paymentForm?.program?.project_title
        return programInput
    }

    createWhenInput(){
        const donateInput = document.createElement('input')
        donateInput.type = 'hidden'
        donateInput.name = 'donate-when'
        donateInput.id = 'donate-when'
        donateInput.value = new URLSearchParams(location.search).get('when') || '6-months'
        return donateInput
    }

    createAmountInput(){
        const amountInput = document.createElement('input')
        amountInput.type = 'hidden'
        amountInput.name = 'amount'
        amountInput.id = 'amount'
        amountInput.value = this.paymentForm?.amounts?.value
        return amountInput
    }
}